import React, {
    Component
} from 'react';
import defaultImage from './images/item-02.jpg';
import { ListGroup } from 'react-bootstrap'

class ProductDetailImage extends Component {
    render() {
        const { image, type=null, index=null } = this.props;
        
        if(type=='dot'){
            return (
                <ListGroup.Item action href={"#image-link"+index} className={'image-dot-item'}>
                    <img src={ image ? image : defaultImage }/>
                    <div class="slick3-dot-overlay"></div>
                </ListGroup.Item> 
            );
        }else{
            return ( 
                <div class="item-slick3">
                    <div class="wrap-pic-w">
                        <img src={ image ? image : defaultImage } alt="IMG-PRODUCT" />   
                    </div> 
                </div>
            );
        }
    }
}

export default ProductDetailImage;
